@import url(https://fonts.googleapis.com/css?family=Passion+One|Rubik&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace; }

* {
  font-family: "Nanum Gothic", sans-serif; }

.map-container {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: space-between; }
  @media only screen and (max-width: 720px) {
    .map-container {
      display: block; } }

.map {
  width: inherit;
  border-right: thin solid #666;
  flex-basis: 70%; }
  @media only screen and (max-width: 720px) {
    .map {
      width: 100%; } }

.inner-map {
  height: 100vh;
  width: 70%;
  position: fixed; }
  @media only screen and (max-width: 720px) {
    .inner-map {
      position: relative;
      width: 100%; } }

.map-card-items {
  width: 30px;
  height: 30px;
  background: #fff;
  border-radius: 100%;
  border: 5px solid #da4c14;
  text-align: center; }

.map-card-text {
  margin-top: 0;
  font-size: 7px;
  font-weight: bold; }

.church-icon {
  width: 20px;
  height: 20px;
  margin-top: 14%; }

.map-item-details {
  flex-basis: 30%; }

.location-details-details {
  width: 70%;
  margin: 5% auto; }

.location-details-header {
  font-family: "Passion One", cursive;
  font-weight: bold;
  color: #666; }

.location-details-divider {
  border-bottom: thin solid #e3e3e3;
  width: 50%; }

.location-details-items,
.location-details-details {
  padding-left: 0; }

.location-details-items .location-details-item {
  list-style: none; }

.location-details-icons {
  width: 20px;
  height: 20px; }

.location-details-icons-details {
  width: 15px;
  height: 15px;
  margin-right: 4%;
  margin-top: 0.9%; }

.location-details-sub-item {
  display: flex; }

.location-details-sub-item-value {
  margin-left: 4%;
  color: #337ab7; }

.operation-date-sub-header {
  text-align: center;
  margin-top: 20%;
  color: #666;
  font-family: "Passion One", cursive; }

.details-divider {
  width: 40%;
  margin: auto;
  border-bottom: thin solid #e3e3e3; }

.operation-time {
  color: #999;
  font-size: 12px; }

.location-details .location-details-list {
  list-style: none;
  display: flex; }

.location-details-list .location-details-icons {
  flex-basis: 20%; }

.location-details-list .location-details-values {
  margin-top: 0; }

.btn-get-direction {
  -webkit-transform: rotate(270deg);
  transform: rotate(270deg);
  padding: 1rem;
  background: #fff;
  cursor: pointer;
  border: 0;
  color: #e3e3e3;
  border: thin solid #e3e3e3;
  font-size: 10px;
  font-weight: bold;
  width: 190px;
  position: absolute;
  right: -4.6%;
  bottom: 10%; }
  .btn-get-direction:hover {
    opacity: 0.5; }
  @media only screen and (max-width: 720px) {
    .btn-get-direction {
      bottom: -50%;
      right: -19%; } }

.logout {
  position: absolute;
  right: 5%;
  top: 2%;
  cursor: pointer;
  font-weight: bold;
  font-size: 10px;
  color: #da4c14; }
  .logout:hover {
    opacity: 0.4; }

.get-direction-cover {
  text-align: center; }

.groups-divider {
  width: 30%;
  border-bottom: thin solid #e3e3e3; }

.detailed-list-area .file-icon:hover {
  opacity: 0.3;
  cursor: pointer; }

.add-detail-inner-cover {
  display: flex;
  justify-content: space-between; }

.add-detail-inner-cover .form-inputs, .add-detail-inner-cover .form-textarea, .add-detail-inner-cover .form-inputs-select {
  flex-basis: 45%; }

.action-btn-add-detail {
  margin-top: 5%;
  margin-bottom: 5%; }

.action-btn-add-detail .new-detail-btn {
  padding: 0.5rem;
  font-size: 10px;
  font-weight: bold; }
  .action-btn-add-detail .new-detail-btn:hover {
    cursor: pointer;
    opacity: 0.5; }

.phoneNumberValue {
  color: #337ab7;
  font-style: italic; }

.float-distance-cover {
  position: absolute;
  right: 3%;
  top: 17%;
  text-align: center; }
  @media only screen and (max-width: 720px) {
    .float-distance-cover {
      margin-top: 160%; } }

.floating-distance-circle {
  border-radius: 100%;
  width: 100px;
  height: 100px;
  border: 5px solid #666;
  margin: 0 auto; }
  @media only screen and (max-width: 720px) {
    .floating-distance-circle {
      width: 70px;
      height: 70px; } }

.floating-distance {
  line-height: 60px;
  color: #666;
  font-family: "Passion One", cursive; }
  @media only screen and (max-width: 720px) {
    .floating-distance {
      line-height: 45px;
      font-size: 20px; } }

.user-direction {
  position: absolute;
  bottom: 0;
  height: 50px;
  width: 10%;
  background: #337ab7;
  cursor: pointer; }
  .user-direction:hover {
    opacity: 0.5; }
  .user-direction .user-direction-text {
    text-align: center;
    line-height: 30px;
    color: #fff;
    font-size: 12px; }
    @media only screen and (max-width: 720px) {
      .user-direction .user-direction-text {
        font-size: 12px; } }
  @media only screen and (max-width: 720px) {
    .user-direction {
      width: 50%; } }

.floating-distance-text {
  text-transform: uppercase;
  font-size: 12px;
  font-weight: bold; }
  @media only screen and (max-width: 720px) {
    .floating-distance-text {
      font-size: 9px; } }

.logo {
  width: 40%;
  margin-top: 10%; }

.logo-text {
  text-transform: uppercase;
  font-family: "Passion One", cursive;
  font-weight: bold; }

.account-cover {
  border-top: thin solid #e3e3e3;
  border-bottom: thin solid #e3e3e3;
  width: 250px;
  height: 250px auto;
  margin: 5% auto;
  padding: 5rem;
  background: #fff; }

.account-title-sub-text {
  color: #999;
  font-size: 15px; }

.divider {
  border-bottom: thin solid #e3e3e3;
  width: 20%;
  margin: 10% auto; }

.quote-author {
  font-style: italic; }

.modal-inner-wrap {
  display: flex;
  flex-direction: row;
  padding: 1rem; }

.modal-right-view {
  flex-basis: 60%;
  padding: 2rem; }

.modal-left-view {
  flex-basis: 40%;
  border: thin solid #e3e3e3;
  padding: 2rem; }

.modal-close-btn {
  position: absolute; }

.upload-file-area {
  border: thin dashed #e3e3e3;
  width: 100%;
  height: 50px;
  text-align: center;
  line-height: 50px;
  color: #e3e3e3; }

.files-inner-area {
  padding-left: 0; }

.files-inner-area .files-item {
  list-style: none;
  display: flex;
  position: relative;
  flex: row 1;
  border-bottom: thin solid #e3e3e35b; }

.file-text {
  margin-left: 4%;
  font-size: 49px;
  font-size: 12px;
  font-weight: bold; }

.remove-file {
  position: absolute;
  right: 1%;
  color: #da4c14;
  cursor: pointer; }

.file-icon {
  width: 15px; }

.form-btns {
  margin-top: 5%; }

.danger {
  background: #da4c14;
  color: #fff; }

.form-groups {
  display: flex;
  flex-direction: column;
  margin-top: 5%; }

.form-input-btn, .form-inputs, .form-textarea, .form-inputs-select, .form-btns {
  box-sizing: border-box;
  box-shadow: none;
  border-radius: 3px; }

.form-inputs, .form-textarea, .form-inputs-select {
  padding: 0.5rem;
  margin-top: 5px;
  border: thin solid #282c36; }

.form-input-lg {
  height: 40px; }

.form-textarea {
  -webkit-appearance: none;
  padding: 1rem;
  width: 100%; }

.form-inputs-select {
  height: 28px;
  border-radius: 3px !important;
  -webkit-appearance: none; }

.form-btns-lg {
  box-shadow: none;
  box-sizing: border-box;
  border: 0;
  color: #666;
  background: #e3e3e3;
  cursor: pointer;
  height: 50px; }
  .form-btns-lg:hover {
    opacity: 0.5; }

.form-btns {
  box-shadow: none;
  box-sizing: border-box;
  height: 35px;
  cursor: pointer; }

.form-btns:active, :focus {
  outline: none;
  text-decoration: none; }

.form-labels {
  font-size: 12px; }


